.service__container{
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    column-gap: 1.8rem;
}

.service__content {
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: var(--container-color);
    padding: 6rem 0 2rem 2.5rem;
}

.service__icon {
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}

.service__title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
}

.service__button {
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}

.sercvice__button-icon {
    font-size: 1rem;
    transition: 2s ease-in-out;
}

.service__button:hover .service__button-icon {
    transform: translateX(00.25rem);
}

.service__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.service__modal-content {
    width: 500px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
}

.service__modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
}

.service__modal-title, .service__modal-description {
    text-align: center;
}

.service__modal-title {
    font-size: var(--h2-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
}

.service__modal-description {
    font-size: var(--small-font-size);
    padding: 0 3.5rem;
    margin-bottom: var(--mb-2);
}

.service__modal-services {
    row-gap: 0.75rem;
}

.service__modal-service {
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
}

.service__modal-service-icon {
    font-size: 1.1rem;
    color: var(--title-color);
}

.service__modal-info {
    font-size: var(--small-font-size);
}

/* Active modal */
.active__modal {
    opacity: 1;
    visibility: visible;
}

/*********************************** MEDIA QUERY */
@media screen and (max-width: 992px){
    .service__container{
        grid-template-columns: repeat(3, 218px);
    }
}

@media screen and (max-width: 768px){
    .service__container{
        grid-template-columns: repeat(2, 1fr);
    }

    .service__content {
        padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    }

    .service__icon {
        font-size: 1.5rem;
    }
}

@media screen and (max-width: 576px){
    .service__modal-content {
        padding: 4.5rem 1.5rem 2.5rem;
    }

    .service__modal-description {
        padding: 0;
    }
}

@media screen and (max-width: 350px){
    .service__container{
        grid-template-columns: max-content;
    }

    .service__content{
        padding-right: 6rem;
    }
}